.play {
  cursor: pointer;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: block;
  width: 0;
  height: 0;
  border-top: 30px solid transparent;
  border-bottom: 30px solid transparent;
  border-left: 36px solid #fff;
  margin: auto;
  z-index: 1;
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  left: 6px;
}
.play:before {
    content: '';
    position: absolute;
    top: -45px;
    left: -69px;
    bottom: -45px;
    right: -21px;
    border-radius: 50%;
    border: 6px solid #fff;
    z-index: 2;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
}
.play:after {
    content: '';
    opacity: 0;
    transition: opacity 0.6s;
    -webkit-transition: opacity 0.6s;
    -moz-transition: opacity 0.6s;
}
.play:hover:before, .play:focus:before {
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
}
.play.active {
    border-color: transparent;
}
.play.active:after {
      content: '';
      opacity: 1;
      width: 30px;
      height: 48px;
      background: #fff;
      position: absolute;
      right: 3px;
      top: -24px;
      border-left: 12px solid #fff;
      -webkit-box-shadow: inset 18px 0 0 0 #f9f9f9;
              box-shadow: inset 18px 0 0 0 #f9f9f9;
}
